/*ʵ����֤*/
.title{
    height: 44px;
    line-height: 44px;
    padding-left:20px;
    font-size: 14px;
    color: #666666;
}
.zz-list{
    display: block;
    background-color: #ffffff;
    font-size: 14px;
    text-align: left;
}
.zz-list li{
    height:48px;
    line-height: 48px;
    border-top:1px solid  #e4e4e4;
    padding-left:20px;
    padding-right: 20px;
    position: relative;
    box-sizing: border-box;
}
.zz-list li:last-child{
    border-bottom:1px solid  #e4e4e4;
}
.zz-list li label{
    color: #303030;
}
.zz-list li .value{
    float: right;
}
.zz-list li .icon{
    position: absolute;
    left: 15px;
    top:11px;
    width: 27px;
    height: 27px;
    background-size: cover;
    background-repeat: no-repeat;
}
.zz-list li label.icontext{
    margin-left: 38px;
}
.zz-list li input[type="text"],.zz-list li input[type="tel"]{
    height: 40px;
    border: 0;
    outline: none;
