/*����*/
.boxPadding{
    padding:0 10px 20px 10px;
    color:#666;
}
.condition-item{
    margin-top:20px;
    width:50%;
    font-size:15px;
    height:30px;
    line-height:30px;
}
.condition-item label{
    width:56%;
    display:inline-block;
}
.sex{
    margin-bottom:10px;
}
.condition-item select{
    height:30px;
    appearance:none;
    -moz-appearance:none;
    -webkit-appearance:none;
    border:1px solid #dedfe0;
    width:calc( 44% - 12px );
    border-radius:4px;
    padding-left:10px;
}
.condition-item input{
    border:1px solid #dedfe0;
    height:30px;
    width:calc( 44% - 12px );
    border-radius:4px;
    padding-left:10px;
}
.detailDialog tr{
    border: 1px solid #dedfe0;
    text-align: center;
}
.detailDialog{
    margin-top:20px;
    width:100%;
    color: #303030;
    font-size:15px;
    border-collapse:collapse;
    border-spacing:0;
}
.detailDialog th{
    height: 30px;
    border: 1px solid #dedfe0;
    text-align: center;
    color:#666;
}
.detailDialog td{
    height: 45px;
    border: 1px solid #dedfe0;
    text-align: center;
}
.trigger{
    color:#4c6ed2;
}
.trigger:first-child{
    display: inline-block;
    margin-right: 10px;
}
.addBox{
    clear: both;
}
.addBtn{
    height: 30px;
    background: #4c6ed2;
    border-radius: 4px;
    border: none;
    color:#fff;
    width: 80px;
}



